header {
  width: 100%;
  position: absolute;
  height: 132px;
  background: url(/img/header/header.png) center top repeat-x;
  z-index: 20;
  margin-top: 43px;
  top: -43px; }
  header nav {
    width: 1100px;
    margin: 0 auto;
    box-sizing: border-box;
    padding-left: 120px;
    transition: padding .3s ease;
    height: 30px;
    line-height: 30px; }
    header nav a {
      display: block;
      float: left;
      margin: 0 15px;
      transition: margin .3s ease;
      text-decoration: none;
      color: #999; }
      header nav a:hover {
        color: #fff; }
  header.fixed {
    top: 0;
