.parallax {
  width: 100%;
  height: 800px;
  position: relative;
  overflow: hidden;
  background: #7f776b; }
  .parallax .controls {
    position: absolute;
    z-index: 999;
    bottom: 50px;
    right: 20px;
    background: rgba(255, 255, 255, 0.2); }
    .parallax .controls:after {
      display: table;
      width: 100%;
      content: '';
      clear: both; }
    .parallax .controls .button {
      height: 18px;
      line-height: 18px;
      font-size: 12px;
      display: block;
      float: left; }
      .parallax .controls .button:hover {
        background: #fff;
        box-shadow: 0 0 0 1px #fff; }
      .parallax .controls .button.active {
        background: #f00;
        color: #fff;
        box-shadow: 0 0 0 1px #f00; }
  .parallax + .section:after {
    content: '';
    width: 100%;
    background: url(/img/header/bottom.png) 0 center repeat-x;
    height: 40px;
    position: absolute;
    top: -40px;
    left: 0; }
  .parallax + .section.section-dark:after {
    background-position: bottom center; }

.section {
  padding: 50px 0;
  z-index: 19;
  position: relative; }
  .section.section-dark {
    background: #f7f6fa; }
  .section .content {
    width: 1100px;
    margin: 0 auto; }
    .section .content h2 {
      font-size: 32px;
      display: inline-block;
      line-height: 30px; }
      .section .content h2 span {
        display: inline-block;
        color: #fff;
        background: #f00;
        padding: 0 8px 0 125px;
        position: relative; }
        .section .content h2 span:before {
          content: '';
